.App {
    height: 100vh;

    .section {
        width: 100vw;
        height: calc(100vh - 70px);
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none;  //for firefox
        &::-webkit-scrollbar{   //for chrome
            display:none;
        }

        
        > * {
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}